ack;\n    font-size: 30px;\n    text-decoration: none;\n    visibility: hidden;\n  }\n#logo {\n  font-size: 30px;\n  color: #f52e14;\n}\n  \n  #link li a:hover {\n    color: $red;\n  }\n\n  #gambarhero {\n    width: 100%;\n    height: 500px;\n    background-color: $red;\n    object-fit: none;\n    left: 0;\n    right: 0;\n    margin: 0;\n  }\n  #hero {\n    width: 100%;\n  }\n  #hero > #hero-text {\n    margin-top: -300px;\n    padding-bottom: 300px;\n    text-align: center;\n    color: white;\n    font-size: 24px;\n    color: white;\n    text-shadow: 2px 2px 4px #000000;\n    padding-left:  10%;\n    padding-right: 10%;\n    button:hover {\n      background-color: $red;\n    }\n    button {\n    background-color:$red;\n    min-height: 44px;\n    min-width: 44px;\n    padding: 0 10px;\n    border: none;\n    border-radius: 5px;\n    cursor: pointer;\n    color: white;\n    a {\n      color: white;\n      text-decoration: none;\n    }\n    }\n  }\n  \n\n  // content\n\n  .poster {\n    img {\n      width: 180px;\n    }\n  }\n\n  #new-event {\n    margin-top: -200px;\n    display: flex;\n    flex-direction: column;\n   align-items: center;\n   gap: 20px;\n   button:hover {\n    background-color: $red;\n  }\n  button {\n  background-color:$red;\n  min-height: 44px;\n  min-width: 44px;\n  padding: 0 10px;\n  border: none;\n  border-radius: 5px;\n  cursor: pointer;\n  color: white;\n  a {\n    color: white;\n    text-decoration: none;\n  }\n  }\n}\n  \n  .event-container {\n    display: flex;\n    flex-direction: row ;\n    gap: 20px;\n  }\n\n  .event-wrapper {\n    display: flex;\n    flex-direction: row ;\n    gap: 20px;\n  }\n\n  .card-event {\n    border: #000000;\n    border-radius: 10px;\n    border-width: 3px;\n    background-color: #b9b9b9;\n    padding: 10px;\n  }\n  .event-info {\n    ul \n    {list-style: none;\n    }\n  }\n#syarat-donor {\n  h2 {\n    text-align: center;\n  }\n}\n.syarat-container {\n  display: grid;\n  grid-template-columns: repeat(2 , 1fr); \n  justify-content: center;\n  align-items: center;\n  padding: 20px 10%;\n  gap: 30px;\n  place-items: center;\n}\n  .syarat-card{\n    padding: 10px;\n    width: 500px;\n    background-color: $red;\n    border-radius: 5px;\n  }\n#keuntungan-donor {\n  h2 {\n    text-align: center;\n  }\n}\n  .keuntungan-container {\n    display: flex;\n    padding: 10%;\n    img {\n   max-width: 300px;\n    }\n    .keuntungan-info {\n      max-width: 800px;\n    }\n  }\n\n  @media screen and (min-width: 650px) {\n    .app-bar {\n      grid-template-columns: 1fr auto;\n      padding: 8px 32px;\n    }\n   \n    .app-bar .app-bar__brand h1 {\n      font-size: 1.5em;\n    }\n   \n    .app-bar .app-bar__menu {\n      display: none;\n    }\n   \n    .app-bar .app-bar__navigation {\n      position: static;\n      width: 100%;\n    }\n   \n    .app-bar .app-bar__navigation ul li {\n      display: inline-block;\n    }\n   \n    .app-bar .app-bar__navigation ul li a {\n      display: inline-block;\n      width: 120px;\n      text-align: center;\n      margin: 0;\n    }\n   \n    .movies {\n      grid-template-columns: 1fr 1fr;\n    }\n   \n    .movie {\n      grid-template-columns: auto 1fr;\n    }\n   \n    .movie .movie__title {\n      grid-column-start: 1;\n      grid-column-end: 3;\n    }\n   \n    .movie .movie__overview {\n      grid-column-start: 1;\n      grid-column-end: 3;\n    }\n  }\n   \n  @media screen and (min-width: 800px) {\n    .app-bar .app-bar__brand h1 {\n      font-size: 2em;\n    }\n  }\n   \n  @media screen and (min-width: 850px) {\n    .movies {\n      grid-template-columns: repeat(3, 1fr);\n    }\n  }\n   \n  @media screen and (min-width: 1200px) {\n    .movies {\n      grid-template-columns: repeat(4, 1fr);\n    }\n  }\n   \n  @media screen and (min-width: 1600px) {\n    .movies {\n        grid-template-columns: repeat(5, 1fr);\n    }\n  }"],"sourceRoot":""}]);
